import { h, Component } from 'preact';
import { ymd } from '../../utils/date';
import { slugify } from '../../utils/slugify';
import { QuestionList } from '../../components/QuestionList';
import { AddQuestion } from '../../components/AddQuestion';
import { ScaryButton } from '../../components/ScaryButton';
import { getDefaultTheme, prefersAnimation } from '../../utils/theme';
import { actions } from '../../store/actions';
import { connect } from 'unistore/preact';
import { getTrackingQuestions } from '../../utils/questions';
import { TrackingQuestionList } from '../../components/TrackingQuestionList';
import { Link } from 'preact-router';

class Settings extends Component {
  state = {
    questions: [],
    exporting: 0,
    importing: false,
    files: [],
    trackingQuestions: [],
  };

  async componentWillMount() {
    const keys = await this.props.db.keys('questions');
    const questions = await Promise.all(
      keys.map(x => this.props.db.get('questions', x))
    );
    questions.sort((a, b) => a.createdAt - b.createdAt);

    const trackingQuestions = await getTrackingQuestions(this.props.db);

    this.setState({ questions, trackingQuestions });
  }

  updateSetting = key => {
    return event => {
      this.props.updateSetting({ key, value: event.target.value });
    };
  };

  updateQuestion = (slug, value, attribute = 'text') => {
    const questions = [...this.state.questions];
    const question = questions.find(x => x.slug === slug);
    if (!question) {
      return;
    }

    question[attribute] = value;
    this.props.db.set('questions', slug, question);

    this.setState({ questions });
  };

  updateQuestionStatus = (slug, value) => {
    this.updateQuestion(slug, value, 'status');
  };

  updateTrackingQuestion = (
    id,
    value,
    attribute = 'text',
    settings = false
  ) => {
    const trackingQuestions = [...this.state.trackingQuestions];
    const question = trackingQuestions.find(x => x.id === id);

    if (!question) {
      return;
    }

    if (settings) {
      question.settings[attribute] = value;
    } else {
      question[attribute] = value;
    }
    this.props.db.set('trackingQuestions', id, question);

    this.setState({ trackingQuestions });
  };

  updateTrackingOption = (id, value, index, remove = false) => {
    const trackingQuestions = [...this.state.trackingQuestions];
    const question = trackingQuestions.find(x => x.id === id);

    if (!question) {
      return;
    }

    if (remove) {
      question.settings.options.splice(index, 1);
    } else {
      question.settings.options[index] = value;
    }

    this.props.db.set('trackingQuestions', id, question);

    this.setState({ trackingQuestions });
  };

  addQuestion = async event => {
    event.preventDefault();
    const text = event.target.question.value;
    const slug = slugify(text);
    const question = { slug, text, status: 'live', createdAt: Date.now() };

    await this.props.db.set('questions', slug, question);

    localStorage.setItem('MindPalace_onboarded', true);
    const questions = [...this.state.questions];
    questions.push(question);
    this.setState({ questions });
    event.target.reset();
  };

  getData = async () => {
    try {
      const questionValues = await this.props.db.getAll('questions');
      const questions = questionValues.reduce((current, value, index) => {
        current[value.slug] = value;
        return current;
      }, {});

      const entries = await this.props.db.getObject('entries');
      const highlights = await this.props.db.keys('highlights');
      const settings = await this.props.db.getObject('settings');
      const trackingEntries = await this.props.db.getObject('trackingEntries');
      const trackingQuestions = await this.props.db.getObject(
        'trackingQuestions'
      );

      return {
        questions,
        entries,
        highlights,
        settings,
        trackingEntries,
        trackingQuestions,
      };
    } catch (e) {
      return {
        questions: {},
        entries: {},
        highlights: [],
        settings: {},
        trackingEntries: {},
        trackingQuestions: {},
      };
    }
  };

  clean = () => {
    if (this.state.files.length) {
      this.state.files.forEach(({ data }) => {
        window.URL.revokeObjectURL(data);
      });
    }
  };

  prepareExport = async () => {
    try {
      const MIME_TYPE = 'text/json;charset=utf-8';

      this.clean();

      this.setState({ exporting: 1, files: [] });

      const data = await this.getData();
      const blob = new Blob([JSON.stringify(data)], { type: MIME_TYPE });

      const file = {
        name: `MindPalace_${ymd()}.json`,
        data: window.URL.createObjectURL(blob),
      };
      this.setState({ files: [file], exporting: 2 });
    } catch (e) {
      console.error(e);
      this.setState({ files: [], exporting: 0 });
    }
  };

  importData = async event => {
    const reader = new FileReader();
    const file = event.target.files[0];
    this.setState({ importing: true });

    reader.onload = (() => async e => {
      const {
        entries,
        questions,
        highlights = [],
        settings = {},
        trackingQuestions = {},
        trackingEntries = {},
      } = JSON.parse(e.target.result);
      if (!entries || !questions || !Array.isArray(highlights)) {
        return;
      }

      const questionKeys = Object.keys(questions);
      questionKeys.map(async key => {
        const current = await this.props.db.get('questions', key);
        if (!current) {
          await this.props.db.set('questions', key, questions[key]);
        }
      });

      const entryKeys = Object.keys(entries);
      await Promise.all(
        entryKeys.map(async key => {
          const current = await this.props.db.get('entries', key);
          if (!current) {
            return this.props.db.set('entries', key, entries[key]);
          }
        })
      );

      const trackingQuestionKeys = Object.keys(trackingQuestions);
      await Promise.all(
        trackingQuestionKeys.map(async key => {
          const current = await this.props.db.get('trackingQuestions', key);
          if (!current) {
            return this.props.db.set(
              'trackingQuestions',
              key,
              trackingQuestions[key]
            );
          }
        })
      );

      const trackingEntryKeys = Object.keys(trackingEntries);
      await Promise.all(
        trackingEntryKeys.map(async key => {
          const current = await this.props.db.get('trackingEntries', key);
          if (!current) {
            return this.props.db.set(
              'trackingEntries',
              key,
              trackingEntries[key]
            );
          }
        })
      );

      const settingKeys = Object.keys(settings);
      await Promise.all(
        settingKeys.map(async key => {
          const current = await this.props.db.get('settings', key);
          if (!current) {
            return this.props.db.set('settings', key, settings[key]);
          }
        })
      );

      await Promise.all(
        highlights.map(async key => this.props.db.set('highlights', key, true))
      );

      localStorage.setItem('MindPalace_onboarded', true);
      localStorage.setItem('MindPalace_dates_migrated', true);

      window.location.reload();
    })();

    reader.readAsText(file);
  };

  deleteData = async () => {
    await this.props.db.clear('entries');
    await this.props.db.clear('questions');
    await this.props.db.clear('highlights');
    await this.props.db.clear('highlights');
    await this.props.db.clear('settings');
    await this.props.db.clear('trackingEntries');
    await this.props.db.clear('trackingQuestions');
    localStorage.removeItem('MindPalace_onboarded');
    localStorage.removeItem('MindPalace_dates_migrated');
    window.location.href = '/';
  };

  render(
    { settings = {} },
    { questions, exporting, files, importing, trackingQuestions }
  ) {
    const theme = settings.theme || getDefaultTheme(settings);
    const animation = settings.animation || prefersAnimation(settings);

    return (
      <div class="wrap lift-children">
        <div>
          <h2 class="mb20">
          <svg
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            width="20"
            height="20"
            viewBox="0 0 24 15"
          >
          <title>Questions</title>
              <path 
              d="M9.896,3.838L0.792,1.562v14.794l9.104,2.276L19,16.356V1.562L9.896,3.838z M9.327,17.332L1.93,15.219V3.27
              l7.397,1.585V17.332z M17.862,15.219l-7.397,2.113V4.855l7.397-1.585V15.219z"
                fill="currentColor"
                >
                </path>
            </svg>  
          Your journaling questions</h2>
          <QuestionList
            questions={questions}
            updateQuestion={this.updateQuestion}
            updateQuestionStatus={this.updateQuestionStatus}
          />
          <Link class="button button--grey" href="/add-journal-question/">
            Add {questions.length ? 'another' : 'a'} question
          </Link>

          <hr />
        </div>

        <div>
          <h2 class="mb20">
          <svg
            xmlns="http://www.w3.org/2000/svg"
            width="20"
            height="20"
            viewBox="0 0 24 24"
            fill="none"
            stroke="currentColor"
            stroke-width="2"
            stroke-linecap="round"
            stroke-linejoin="round"
          >
            <title>Stats</title>
            <polyline points="22 12 18 12 15 21 9 3 6 12 2 12" />
          </svg>  
          Your personal statistics</h2>
          <TrackingQuestionList
            trackingQuestions={trackingQuestions}
            updateTrackingQuestion={this.updateTrackingQuestion}
            updateTrackingOption={this.updateTrackingOption}
          />
          <Link class="button button--grey" href="/add-statistic/">
            Add {trackingQuestions.length ? 'another' : 'a'} personal statistic
          </Link>
          <hr />
        </div>

        <div className="mb20">
          <p>
            <label for="theme">
            <svg
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            width="20"
            height="20"
            viewBox="0 0 24 15"
          >
          <title>Theme</title>
            <path
              d="M10,0.625c-5.178,0-9.375,4.197-9.375,9.375S4.822,19.375,10,19.375s9.375-4.197,9.375-9.375S15.178,0.625,10,0.625 M10,18.522V1.478c4.707,0,8.522,3.815,8.522,8.522S14.707,18.522,10,18.522"
              fill="currentColor"
            >
            </path>
            </svg>
            Theme</label>
            <select
              id="theme"
              onChange={this.updateSetting('theme')}
              value={theme}
            >
              <option value="dark">default</option>
              <option value="light">Light</option>
              <option value="dark">Dark</option>
            </select>
          </p>

          <p>
            <label for="animation">
            <svg
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            width="20"
            height="20"
            viewBox="0 0 20 14"
          >
          <title>Animation</title>
          <path
          d="M19.305,9.61c-0.235-0.235-0.615-0.235-0.85,0l-1.339,1.339c0.045-0.311,0.073-0.626,0.073-0.949
          c0-3.812-3.09-6.901-6.901-6.901c-2.213,0-4.177,1.045-5.44,2.664l0.897,0.719c1.053-1.356,2.693-2.232,4.543-2.232
          c3.176,0,5.751,2.574,5.751,5.751c0,0.342-0.037,0.675-0.095,1l-1.746-1.39c-0.234-0.235-0.614-0.235-0.849,0
          c-0.235,0.235-0.235,0.615,0,0.85l2.823,2.25c0.122,0.121,0.282,0.177,0.441,0.172c0.159,0.005,0.32-0.051,0.44-0.172l2.25-2.25
          C19.539,10.225,19.539,9.845,19.305,9.61z M10.288,15.752c-3.177,0-5.751-2.575-5.751-5.752c0-0.276,0.025-0.547,0.062-0.813
          l1.203,1.203c0.235,0.234,0.615,0.234,0.85,0c0.234-0.235,0.234-0.615,0-0.85l-2.25-2.25C4.281,7.169,4.121,7.114,3.961,7.118
          C3.802,7.114,3.642,7.169,3.52,7.291l-2.824,2.25c-0.234,0.235-0.234,0.615,0,0.85c0.235,0.234,0.615,0.234,0.85,0l1.957-1.559
          C3.435,9.212,3.386,9.6,3.386,10c0,3.812,3.09,6.901,6.902,6.901c2.083,0,3.946-0.927,5.212-2.387l-0.898-0.719
          C13.547,14.992,12.008,15.752,10.288,15.752z"
          fill="currentColor"
              >
              </path>
              
            </svg>
            Animation</label>
            <select
              id="animation"
              onChange={this.updateSetting('animation')}
              value={animation}
            >
              <option value="off">default</option>
              <option value="on">On</option>
              <option value="off">Off</option>
            </select>
          </p><hr/></div>

          <div id="data">
          <h2>
          <svg
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            width="20"
            height="20"
            viewBox="0 0 24 15"
          >
          <title>Data</title>
          <path d="M5.029,1.734h10.935c0.317,0,0.575-0.257,0.575-0.575s-0.258-0.576-0.575-0.576H5.029
								c-0.318,0-0.576,0.258-0.576,0.576S4.711,1.734,5.029,1.734z M1,5.188V19h18.417V5.188H1z M18.266,17.849H2.151V6.338h16.115
								V17.849z M2.727,4.036H17.69c0.317,0,0.575-0.257,0.575-0.576c0-0.318-0.258-0.575-0.575-0.575H2.727
								c-0.318,0-0.576,0.257-0.576,0.575C2.151,3.779,2.409,4.036,2.727,4.036z"
              fill="currentColor"
              >
              </path>
            </svg> 
            Manage your data</h2>

          {exporting === 2 && files.length ? (
            <a
              class="button button--space"
              download={files[0].name}
              href={files[0].data}
              onClick={() => {
                setTimeout(() => {
                  this.clean();
                  this.setState({ exporting: 0 });
                }, 1500);
              }}
            >
              Click to Download
            </a>
          ) : (
            <button
              type="button"
              class={`button button--space button--grey`}
              onClick={this.prepareExport}
            >
              {['Backup', 'Backing up'][exporting]}
            </button>
          )}

          <input
            type="file"
            class="screen-reader-only"
            id="import"
            onChange={this.importData}
            accept="application/json"
          />
          <label for="import" class="button button--grey">
            {importing ? 'Importing...' : 'Import'}
          </label>

          <hr />
        </div>

          <div className="mb20">
          <p style="margin: 0 0 8px;">
          <svg
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            width="50"
            height="50"
            viewBox="3 0 30 28"
          >
            <title>About</title>
            <path
              d="M 16 3 C 8.832031 3 3 8.832031 3 16 C 3 23.167969 8.832031 29 16 29 C 23.167969 29 29 23.167969 29 16 C 29 8.832031 23.167969 3 16 3 Z M 16 5 C 22.085938 5 27 9.914063 27 16 C 27 22.085938 22.085938 27 16 27 C 9.914063 27 5 22.085938 5 16 C 5 9.914063 9.914063 5 16 5 Z M 15 10 L 15 12 L 17 12 L 17 10 Z M 15 14 L 15 22 L 17 22 L 17 14 Z"
              fill="currentColor"
            />
          </svg>

          </p>
          <Link class="button button--grey" href="/about/">
          About MindPalace
          </Link>
          </div>

          <div class="mb40">
            <svg
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            width="50"
            height="50"
            viewBox="0 0 24 24"
          >
          <title>Death Star</title>
            <path
              d="M19.629,9.655c-0.021-0.589-0.088-1.165-0.21-1.723h-3.907V7.244h1.378V6.555h-2.756V5.866h2.067V5.177h-0.689V4.488h-1.378V3.799h0.689V3.11h-1.378V2.421h0.689V1.731V1.294C12.88,0.697,11.482,0.353,10,0.353c-5.212,0-9.446,4.135-9.629,9.302H19.629z M6.555,2.421c1.522,0,2.756,1.234,2.756,2.756S8.077,7.933,6.555,7.933S3.799,6.699,3.799,5.177S5.033,2.421,6.555,2.421z"
              fill="currentColor"
            >
            </path>
            <path
              d="M12.067,18.958h-0.689v-0.689h2.067v-0.689h0.689V16.89h2.067v-0.689h0.689v-0.689h-1.378v-0.689h-2.067v-0.689h1.378v-0.689h2.756v-0.689h-1.378v-0.689h3.218c0.122-0.557,0.189-1.134,0.21-1.723H0.371c0.183,5.167,4.418,9.302,9.629,9.302c0.711,0,1.401-0.082,2.067-0.227V18.958z"
              fill="currentColor"
            >
            </path>
            </svg>
          <ScaryButton onClick={this.deleteData}>Delete your data</ScaryButton>
          </div>
          </div>

    );
  }
}

export default connect(
  'settings, db',
  actions
)(Settings);



